body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.background1{
	background-color: #bcd6f0;/*rgb(189, 214, 239);*/
	min-height: 96vh;
	width: 100vw;
	background-position: relative;
	padding-top: 1px;
	background-image: url(/tmp/static/media/startuplabs.20b447ad.png);
	background-repeat: no-repeat;
	background-size: 10vw;	
	background-position: 7% 10%;
}

.words{
	margin: 10vh auto;
	margin-bottom: 5vh;
	text-align: center;
	font-size: 10em;
	font-weight: bold;
	color: #f0768f;/*rgb(240, 118, 143);*/
	-webkit-animation: floating 1.75s ease-in-out infinite;
	        animation: floating 1.75s ease-in-out infinite;
	cursor: default;
}

@-webkit-keyframes floating {
    0% {text-shadow: 1px 1px 1px gray;}
    50% {text-shadow: 5px 5px 5px gray;}
    100% {text-shadow: 1px 1px 1px gray;}
}

@keyframes floating {
    0% {text-shadow: 1px 1px 1px gray;}
    50% {text-shadow: 5px 5px 5px gray;}
    100% {text-shadow: 1px 1px 1px gray;}
}

.apply_button{
	width: -webkit-max-content;
	width: -moz-max-content;
	width: max-content;
	padding: 1em 2em;
	color: white;
	text-align: center;
	margin: auto auto;
	font-size: 4.1vh;
	font-weight: bold;
	-webkit-transition: 1s;
	transition: 1s;
	background-color: #f0768f;
}

a{ 
	color: white; 
	text-decoration: none;
}

.apply_button:hover{
	box-shadow: 0 25px 15px 0px gray;
	-webkit-transform: translateY(-26px);
	        transform: translateY(-26px);
	background-color: purple;
	cursor: pointer;
}


@media only screen and (max-width: 1000px) {
   .words { 
      font-size: 7em; 
   }
   .apply_button{
   	 font-size: 2em
   }

}
