.button{
	margin-top: 10vh;
	text-align: center;
	width: -webkit-max-content;
	width: -moz-max-content;
	width: max-content;
	padding: 10px 20px;
	cursor: pointer;
	font-weight: bold;
	border-radius: 5px;
	-webkit-transition: 0.2s;
	transition: 0.2s;
}

.button:hover{
	-webkit-animation: companyButton 0.7s ease-in-out infinite;
	        animation: companyButton 0.7s ease-in-out infinite;
}

@-webkit-keyframes companyButton {
    0% {
    	box-shadow: 0 2px 5px 0px rgba(0,0,0,0.6);
		-webkit-transform: translatey(0px);
		        transform: translatey(0px);
	}
    50% {
    	box-shadow: 0 5px 10px 0px rgba(0,0,0,0.4);
		-webkit-transform: translatey(-1px);
		        transform: translatey(-1px);
	}
    100% {
    	box-shadow: 0 2px 5px 0px rgba(0,0,0,0.6);
		-webkit-transform: translatey(0px);
		        transform: translatey(0px);
	}
}

@keyframes companyButton {
    0% {
    	box-shadow: 0 2px 5px 0px rgba(0,0,0,0.6);
		-webkit-transform: translatey(0px);
		        transform: translatey(0px);
	}
    50% {
    	box-shadow: 0 5px 10px 0px rgba(0,0,0,0.4);
		-webkit-transform: translatey(-1px);
		        transform: translatey(-1px);
	}
    100% {
    	box-shadow: 0 2px 5px 0px rgba(0,0,0,0.6);
		-webkit-transform: translatey(0px);
		        transform: translatey(0px);
	}
}

